* {
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

#root {
  display: flex;
  justify-content: center;
}

.container {
  padding: 0 2rem;
}

.App {
  margin: 40px 0;
  width: 800px;

  & form {
    margin-top: 20px;
    display: flex;
  }
}

.todo-list {
  margin-top: 20px;

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    & p {
      margin-right: 15px;
    }

    &_done {
      & p {
        text-decoration: line-through;
      }
    }

    &:not(:first-of-type) {
      margin-top: 10px;
    }

    & button {
      margin-left: auto;

      &:not(:first-of-type) {
        margin-left: 20px;
      }
    }
  }
}

.clear-list {
  margin-top: 10px;
}